import React, { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

function Datepicker() {
  const [selectedRange, setSelectedRange] = useState([null, null]);

  const handleDateChange = (index, date) => {
    const updatedRange = [...selectedRange];
    updatedRange[index] = date;

    if (index === 0 && updatedRange[1] < date) {
      updatedRange[1] = date;
    }

    setSelectedRange(updatedRange);
  };

  const minEndDate = selectedRange[0] || null;

  return (
    <div>
      <h2>Hotel Reservation</h2>
      <div>
        <label>Reservation Dates:</label>
        <DatePicker
          selected={selectedRange[0]}
          onChange={(date) => handleDateChange(0, date)}
          selectsStart
          startDate={selectedRange[0]}
          endDate={selectedRange[1]}
          minDate={new Date()}
        />
        <DatePicker
          selected={selectedRange[1]}
          onChange={(date) => handleDateChange(1, date)}
          selectsEnd
          startDate={selectedRange[0]}
          endDate={selectedRange[1]}
          minDate={minEndDate}
        />
      </div>
    </div>
  );
}

export default Datepicker;