import { useState } from 'react';
import s from './inquiryPopup.module.css';
import {FaAngleUp, FaAngleDown, FaComments}  from 'react-icons/fa6';
const InquiryPopup = () => {
    const [isOpenPoopup, setIsOpenPoopup] = useState(true);
    return (
        <>
            <div className={`${s.wrpper}`}>
                {!isOpenPoopup ? 
                <div className={`${s.popup_wrap}`}>
                    <div className={`${s.container}`}>
                        <div className={`${s.text1}`}>
                            <h2>GV</h2>
                            <p>Residence</p>
                        </div>
                        <div className={`${s.content}`}>
                            <div className={`${s.title_wrap}`}>
                                <div className={`${s.img_box}`}>
                                    <img src={require("../../../assets/images/logo_icon.png")} alt="logo" />
                                </div>
                                <p>GV Residence</p>
                            </div>
                            <p>
                                안녕하세요 GV Residence입니다<br />
                                오늘도 GV Residenc를 이용해주셔서 감사합니다.<br /><br />

                                문의할 사항을 아래 전화로 연락주시면 <br />
                                자세히 안내해드리도록 하겠습니다!<br /><br />

                                빠른 문의 사항을 원하시면 고객문의 페이지를 참고하여 주시기 바랍니다.<br />
                                <br />
                                감사합니다.
                            </p>
                        </div>
                        <button className="c-btn-main">고객문의 페이지로 이동</button>
                        <p className={`${s.phoneNum}`}>전화번호 : 02-797-5880</p>
                    </div>

                </div>
                : ''}
                <div className={`${s.float_wrap}`} onClick={() => setIsOpenPoopup(!isOpenPoopup)}>
                    {
                    isOpenPoopup ? 
                        <div className={`${s.open_wrap}`}>
                            <div className={`${s.inquiry}`}>문의하기</div>   
                            <div className={`${s.img_box}`}>
                                    <img src={require("../../../assets/images/logo_icon.png")} alt="logo" />
                            </div>
                        </div> : 
                        <div className={`${s.close_wrap}`}><FaAngleDown color='#626263'></FaAngleDown></div>
                    }
                    
                    
                    
                </div>
            </div>
        </>
    )
}

export default InquiryPopup;

