import { BrowserRouter, Route, Routes, useLocation } from 'react-router-dom';
import SideMenu from './side-menu/side-menu'
import UserEdit from './user-edit/user-edit'
import UserInfoEdit from './user-edit/user-info-edit'
import BookingSelect from './booking/booking'
import './my-page-layout.css'
import InquiryPopup from './inquiry/inquiryPopup';
import { FaAlignJustify } from 'react-icons/fa6';
import { useCommonContext } from '../../contexts/Common';

const MyPageLayout = () => {

    const location = useLocation().pathname;
    const nowPage = location.split('/')[2];
    const { isShowSideMenu, setisShowSideMenu } = useCommonContext();
    const urlMatch = {
        'user-edit': {
            large: '설정',
            menu: '회원 정보'
        },
        'user-infoedit': {
            large: '설정',
            menu: '회원 정보'
        },
        'booking-select': {
            large: '예약 관리',
            menu: '예약 정보'
        },
    };

    const { large, menu } = urlMatch[nowPage];

    return (
        <div className="mypage-wrap">
            <div className="mypage-title">
                <div className="container">
                    {!isShowSideMenu ?
                        <div className="sideOpenIcon" onClick={() => setisShowSideMenu(true)}>
                            <FaAlignJustify color='#fff'></FaAlignJustify>
                        </div> : ''
                    }
                    {large + " - " + menu}
                </div>
            </div>
            <div className="container">
                <div className="mypage-content">
                    <SideMenu></SideMenu>

                    <div className="page-wrap">
                        <div className="menu-name">
                            {menu}
                        </div>

                        <Routes>
                            <Route path="/user-edit" element={<UserEdit />} />
                            <Route path="/user-infoedit" element={<UserInfoEdit />} />
                            <Route path="/booking-select" element={<BookingSelect />} />
                        </Routes>
                    </div>
                </div>
            </div>
            <div className="inquiryPopup_wrap">

                <InquiryPopup></InquiryPopup>
            </div>
        </div>
    )
};

export default MyPageLayout;