import React from 'react'
import useConfig from '../hooks/useConfig';
import { useCallback } from 'react';
import { FormattedMessage } from 'react-intl';

function LanguageSelect(props) {
    const { onChange, ...rest } = props;
    const { language, onChangeLocalization } = useConfig();

    const handleLanguageChange = useCallback(($evnet) => {
        onChangeLocalization($evnet.target.value);
        onChange?.($evnet);
    }, [onChangeLocalization]);

    return (
        <select {...rest} value={language} onChange={handleLanguageChange}>
            <option value="ko"><FormattedMessage id='korean' /></option>
            <option value="en"><FormattedMessage id='english' /></option>
        </select>
    )
}

export default LanguageSelect
