import s from './user-edit.module.css'

const UserEdit = () => (
    <>
    <div className={`${s.subtext}`}>외부로부터 고객님의 정보를 안전하게 보호하기 위해 비밀번호를 다시 한 번 확인합니다.</div>
    <form name="user-comfirm" className={s.signform} action="" >
        <div className={s.sign_item}>
            <label htmlFor='password' className={s.item_title}>비밀번호</label>
            <input id='password' className='c-input' type="text" placeholder='비밀번호를 입력하세요'/>
        </div>
        <div className={s.topline}>
        </div>
        <button className='c-btn-main'>확인</button>
    </form>
    </>
)

export default UserEdit; 