import s from './booking.module.css'

const BookingSelect = () => (
    <>
        <div className={s.topline}></div>
        <form name="user-comfirm" className={s.signform} action="" >

            <div className={s.two_item}>
                <div className={s.sign_item}>
                    <label htmlFor='bookingId' className={s.item_title}>에약번호</label>
                    <input id='bookingId' className='c-input' type="text" placeholder='에약번호을 입력하세요.'/>
                </div>
                <div className={s.sign_item}>
                    <label htmlFor='roomSevice' className={s.item_title}>룸서비스</label> 
                    <input id='roomSevice' className='c-input' type="text" placeholder='룸서비스을 입력하세요.'/>
                </div>
                
            </div>

            <div className={s.two_item}>
                <div className={s.sign_item}>
                    <label htmlFor='checkIn' className={s.item_title}>체크인</label>
                    <input id='checkIn' className='c-input' type="text" placeholder='체크인을 입력하세요.'/>
                </div>
            </div>

            <div className={s.sign_item}>
                <label  className={s.item_title}>옵션</label> 
                {/*    <input id='roomSevice' className='c-input' type="text" placeholder='룸서비스을 입력하세요.'/> */}
            </div>

            <div className={s.notice_text}>*예약 취소하는경우 다른 사용자가 회원님이 취소하려는 방을 예약할경우 동일한 날짜의 룸타입을 선택하실 수 없을 수 있습니다. 그래도 예약을 취소하시겠습니까?</div>
            <button className='c-btn-black'>에약취소</button>
            
        </form>
    </>
)

export default BookingSelect;       