import { useContext, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import style from './header.module.css'
import { useLocation } from 'react-router-dom';
import { FaAngleRight, FaAlignRight, FaAlignJustify, FaMessage } from 'react-icons/fa6';
import { FormattedMessage } from 'react-intl';
import { useCallback } from 'react';
import LanguageSelect from '../../components/LanguageSelect';
import { menuOpenContext, useCommonContext } from '../../contexts/Common';
import { async } from 'q';


function Header(props) {

    const location = useLocation();
    const pathName = location.pathname;
    const isHome = pathName === '/';
    const {isOpenMenu, isShowMenu, toggleMenu, timer} = useCommonContext();
    const [isActive, setIsActive] = useState(false);

    const onHeaderToggle = () => {
        const windowH = window.innerHeight;
        const scrollY = window.scrollY + 40;

        setTimeout(() => {
            if (scrollY >= windowH) {
                setIsActive(true);
            } else {
                setIsActive(false);
            }
        }, 200);
    }



    useEffect(() => {

        if (!isHome) {
            setIsActive(true)
            return;
        }

        window.addEventListener("scroll", onHeaderToggle);
        setIsActive(false);

        return () => {
            window.removeEventListener("scroll", onHeaderToggle);
        }

    }, [isHome]);

    useEffect(() => {
        (async () => {
            if (isOpenMenu) {
                await timer(100);
                toggleMenu();
            };  
        })();
    
    }, [pathName]);

    return (
        <header className={isActive ? `${style.active}` : ''}>
            <div className={`${style.container} pc flex`}>
                <div className={`${style.logo}`}>
                    <div className={`${style.img_box}`}>
                        {/* <h2> */}
                            <Link to="/">
                                <img src={require("../../assets/images/logo.png")} alt="logo" />
                            </Link>
                        {/* </h2> */}
                    </div>
                </div>
                <div className={`${style.menu_wrap}`}>
                    <div className={`${style.top_menu} flex`}>
                        <Link to="/login"><FormattedMessage id='login' /> </Link>
                        <div>|</div>
                        <Link to="/roominfo"><FormattedMessage id='check-resv' /> </Link>
                        <div>|</div>
                        <div className={`${style.selct_wrap}`}>
                            <i class="fa  fa-globe" ></i>
                            <LanguageSelect />
                        </div>
                    </div>
                    <div className={`${style.bottom_menu} flex`}>
                        <Link to="/" className={`${style.item}`}>HOME</Link>
                        <Link to="/roominfo" className={`${style.item}`}>방 소개</Link>
                        <Link to="/userQnA" className={`${style.item}`}>고객문의</Link>
                    </div>
                </div>
            </div>

            <div className={`${style.mobile_header} mobile`}>
                <div className={`${style.show_menu}`}>
                    <div className={`${style.img_wrap}`}>
                        <Link to="/">
                            <img src={require("../../assets/images/logo.png")} alt="logo" />
                        </Link>
                    </div>
                    <div className={style.icon_wrap} onClick={toggleMenu}>
                        <FaAlignJustify color='#fff'></FaAlignJustify>
                    </div>
                </div>
                {isShowMenu ||
                    <div className={`${style.mobile_container} flex`}>
                        <div className={`${style.menu_layer} ${isOpenMenu ? style.open : style.off}`}>
                            <div className={`${style.mobile_wrpper} `}>
                                <div className={`${style.modile_menu_wrap} flex`}>
                                    <div className={`${style.menu_content}`}>
                                        <Link to="/"><img src={require("../../assets/images/logo.png")} alt="logo" /></Link>
                                        <div className={style.menu_icon} onClick={toggleMenu}>
                                            <FaAlignRight color='#fff' size="3vh"></FaAlignRight>
                                        </div>
                                    </div>
                                    <div className={`${style.top_menu} flex`}>
                                        <Link to="/login">
                                            <div className={`${style.user_info_wrap}`}>
                                                <div className={style.user_info}>
                                                    로그인해주세요
                                                    <div className="left_icon"><FaAngleRight></FaAngleRight></div>
                                                </div>
                                            </div>
                                        </Link>
                                        <Link to="/" className={style.menu_item}>
                                            <div className={`${style.item}`}>HOME</div>
                                            <div className="left_icon"><FaAngleRight></FaAngleRight></div>
                                        </Link>
                                        <Link to="/roominfo" className={style.menu_item}>
                                            <div className={`${style.item}`}>방 소개</div>
                                            <div className="left_icon"><FaAngleRight></FaAngleRight></div>
                                        </Link>
                                        <Link to="/userQnA" className={style.menu_item}>
                                            <div className={`${style.item}`}>고객문의</div>
                                            <div className="left_icon"><FaAngleRight></FaAngleRight></div>
                                        </Link>
                                        <Link to="/roominfo" className={style.menu_item}>
                                            <div className={`${style.item}`}>예약확인</div>
                                            <div className="left_icon"><FaAngleRight></FaAngleRight></div>
                                        </Link>

                                    </div>
                                    <div className={`${style.bottom_menu}`}>

                                        <div className={`${style.select_wrap}`}>
                                            <LanguageSelect />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                }
            </div>
        </header>
    )
}

export default Header;

