import React, { useState, useEffect } from 'react';


function NationSelect(props) {

  const [countries, setCountries] = useState([]);
  const [selectedCountry, setSelectedCountry] = useState('');

  useEffect(() => {
    // 국가 데이터 가져오는 로직 (예: API 호출 등)
    // 이 예시에서는 미리 정의된 국가 목록을 사용합니다.
    const fetchCountryData = async () => {
      // 예시: https://restcountries.com/v3.1/all
      const response = await fetch('https://restcountries.com/v3.1/all');
      const data = await response.json();
      const countryNames = data.map((country) => country.name.common);
      // 국가 이름을 오름차순으로 정렬
      const sortedCountryNames = countryNames.sort((a, b) => a.localeCompare(b));
      setCountries(sortedCountryNames);
    };

    fetchCountryData();
  }, []);

  const handleCountryChange = (event) => {
    setSelectedCountry(event.target.value);
  };

  return (
    <div>
      <select value={props.value ? props.value : selectedCountry} onChange={handleCountryChange} className="c-select">
        <option value="">Select a country</option>
        {countries.map((country, index) => (
          <option key={index} value={country}>
            {country}
          </option>
        ))}
      </select>
    </div>
  );
}

export default NationSelect;
