import { Link } from 'react-router-dom';
import s from './login.module.css'

const Login = () => (
    <div className={`${s.login_form_wrap}`}>
        <div className={`${s.logo}`}>
            <img src={require("../../../assets/images/logo.png")} alt="logo" />
        </div>
        <form action="" name="loginForm" >
            <div className={`${s.item}`}>
                <label htmlFor="login">Eamil</label>
                <input type="text" id="login" placeholder="" />
            </div>
            <div className={`${s.item}`}>
                <label htmlFor="password">Password</label>
                <input type="text" id="password" placeholder="" />
            </div>
            <div className={`${s.item}`}>
                <button>로그인</button>
            </div>
        </form>
        <div className={`${s.other_btn_wrap}`}>
            <div className={`${s.other_btn}`}>
                <Link to="/join">
                    회원가입
                </Link>
            </div>
            {/* <div className={`${s.other_btn}`}>
                아이디 찾기
            </div> */}
        </div>
        <div className={`${s.sns_login_wrap}`}>
            {/* <div className={`${s.sns_btn}`}>
                <i class="fa fab fa-facebook-f"></i>
                <div className={`${s.sns_text1}`}>facebook</div>
            </div>
            <div className={`${s.sns_btn}`}>
                <i class="fa fab fa-google"></i>
                <div className={`${s.sns_text1}`}>Google</div>
            </div> */}
        </div>


    </div>
)

export default Login;