import './index.css';

import { BrowserRouter, Route, Routes, useLocation } from 'react-router-dom';

import Header from './template/layout/header';
import Footer from './template/layout/footer';
import Main from './template/main/main';
import Login from './template/sign/login/login';
import Join from './template/sign/join-member/join-member';
import RoomList from './template/room/room-list/room-list';
import MyPageLayout from './template/mypage/my-page-layout';
import Datepicker from './components/datepiker';
import { ConfigProvider } from './contexts/config';
import { CommonContextProvider } from './contexts/Common';
import Locales from './components/Locales';

function App() {



  return (
    <BrowserRouter>
      <CommonContextProvider >
        <ConfigProvider>
          <Locales>
            <Header />
            <Routes>
              <Route path="/" element={<Main />} />
              <Route path="/login" element={<Login />} />
              <Route path="/join" element={<Join />} />
              <Route path="/roominfo" element={<RoomList />} />
              <Route path="/mypage/*" element={<MyPageLayout />} />

            </Routes>
            <Footer />
          </Locales>
        </ConfigProvider>
      </CommonContextProvider>
    </BrowserRouter>
  );
}

export default App;
