import { createContext, useContext, useState } from "react";

const CommonContext = createContext();


export const CommonContextProvider = ({ children }) => {
    const timer = (time) => new Promise((resolve) => setTimeout(() => { resolve() }, time));
    // 메인메뉴
    const [isOpenMenu, setisOpenMenu] = useState(false);
    const [isShowMenu, setisShowMenu] = useState(true);
    const [isShowSideMenu, setisShowSideMenu] = useState(true);
    const toggleMenu = async () => {
        setisOpenMenu(!isOpenMenu);
        !isOpenMenu || await timer(800);
        setisShowMenu(!isShowMenu);
    };

    const contextValue = {
        timer, toggleMenu, isOpenMenu, isShowMenu, isShowSideMenu, setisShowSideMenu
    };

    return (
        <CommonContext.Provider value={contextValue}>
            {children}
        </CommonContext.Provider>
    );
};

export const useCommonContext = () => {
    // Context를 사용하여 유틸 함수를 반환
    const context = useContext(CommonContext);
    if (!context) {
        throw new Error('CommonContext must be used within a CommonContext');
    }
    return context;
};