import NationSelect from '../../../components/nation-selectbox';
import s from './user-info-edit.module.css'

const UserInfoEdit = () => (
    <>
        <form name="user-comfirm" className={s.signform} action="" >
            <div className={s.signbtn_item}>
                <div className={s.sign_item}>
                    <label htmlFor='email' className={s.item_title}>이메일</label>
                    <input id='email' className='c-input' type="text" placeholder='이메일을 입력하세요.' />
                </div>
                <button>중복확인</button>
                <button>인증번호 요청</button>
            </div>
            <div className={s.signbtn_item}>
                <div className={s.sign_item}>
                    <input id='code' className='c-input' type="text" placeholder='인증번호를 입력해주세요.' />
                </div>
                <button>인증번호 확인</button>
            </div>
            <div className={s.two_item}>
                <div className={s.sign_item}>
                    <label htmlFor='email' className={s.item_title}>전화번호</label>
                    <input id='email' className='c-input' type="text" placeholder='전화번호을 입력하세요.' />
                </div>
                <div className={s.sign_item}>
                    <label htmlFor='email' className={s.item_title}>생년월일</label>
                    <input id='email' className='c-input' type="date" placeholder='생년월일을 입력하세요.' />
                </div>
            </div>
            <div className={s.sign_item}>
                <label htmlFor='email' className={s.item_title}>국적</label>
                <NationSelect value="aaa"></NationSelect>
            </div>

            <div className={s.topline}></div>
            <div className={s.btn_wrap}>
                <button className='c-btn-black'>취소</button>
                <button className='c-btn-main'>확인</button>
            </div>
            <div className={s.topline}></div>
            <div className={s.out_wrap}>
                <div>탈퇴</div>
                <svg xmlns="http://www.w3.org/2000/svg" height="23" viewBox="0 -960 960 960" width="48"><path d="m304-82-56-57 343-343-343-343 56-57 400 400L304-82Z" /></svg>
            </div>
        </form>
    </>
)

export default UserInfoEdit;       