import { BrowserRouter, Link, Route, Routes, useLocation } from 'react-router-dom';
import s from './side-menu.module.css';
import { useState } from 'react';
import { FaX } from 'react-icons/fa6';
import { menuOpenContext, useCommonContext } from '../../../contexts/Common';
import { useEffect } from 'react';

const SideMenu = () => {
    const [activeMenu, setACtiveMenu] = useState(0);
    const [isMobile, setIsMobile] = useState(false);
    const { isShowSideMenu, setisShowSideMenu } = useCommonContext();
    const Menu = [
        {
            topMenu: 0,
            url: "booking-select",
            name: "에약 조회"
        },
        {
            topMenu: 0,
            url: "booking-select",
            name: "예약 수정"
        },
        {
            topMenu: 1,
            url: "user-edit",
            name: "회원 정보"
        },
        {
            topMenu: 1,
            url: "user-infoedit",
            name: "고객 문의"
        },
    ];
    const fixUrl = "/mypage";

    useEffect(() => {
        window.addEventListener("resize", () => {
            const width = window.innerWidth;
            const isPcChk = width >= 1024;
            setisShowSideMenu(isPcChk);
            setIsMobile(!isPcChk);

        });
    });

    const menuMapFn = (topMenuIdx) => ({ topMenu, url, name }, idx) => topMenu === topMenuIdx ?
        <div
            onClick={() => {
                if (isMobile) {
                    setisShowSideMenu(false)
                }
                setACtiveMenu(idx);
            }}
            className={`${s.menu_item} ${idx === activeMenu ? s.active : ''}`}>
            <Link to={`${fixUrl}/${url} `}>{name}</Link>
        </div > : '';


    return (
        <>
            {isShowSideMenu ?
                <div className={`${s.menu_wrap}`}>
                    <div className={`${s.icon_wrap}`} onClick={() => setisShowSideMenu(false)}>
                        <FaX size='16px' color='#fff'></FaX>
                    </div>
                    <div className={`${s.menu_items} `}>
                        <div className={`${s.menu_title} `}>예약 관리</div>
                        {Menu.map(menuMapFn(0))
                        }
                    </div >
                    <div className={`${s.menu_items}`}>
                        <div className={`${s.menu_title}`}>설정</div>
                        {Menu.map(menuMapFn(1))}

                    </div>
                </div >
                : ''}

        </>

    )
}

export default SideMenu;